import React from "react";

import "./cardThree.css";

import pic1 from "./pic1.jpg";

import { Link } from "react-router-dom";

export default function CardThree(prop) {
  return (
    <div className="card-three-main">

        <>
          <div className="card-three-first-div-top"></div>
          <div
            className="card-three-first-div"
          >
            <h1>{prop.heading}</h1>

            <h3>{prop.paragraph}</h3>

            <button><Link to={prop.src}>Learn More</Link></button>
          </div>
        </>


    </div>
  );
}

CardThree.defaultProps = {
  backgroundImage: pic1,
};
