import React from "react";

import "./PortfolioModalTwo.css";

export default function PortfolioModalTwo(prop) {
  return (
    <div
      className="portfolio-modal-two-main-container-first"
      onClick={() => {
        prop.closeModal();
      }}
    >
      <div className="portfolio-modal-two-main-container">
        <img src={prop.picture} alt="work" />
      </div>
    </div>
  );
}
